.mainBlock {
    height: 100vh;
    background-color: #fafafa;
}
.container {
    height: 100%;
    width: 80%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
}
.text {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.text span,.text p{
    color: #211006;
    font-size: 16px;
    font-weight: 500;
    font-family: 'Montserrat', sans-serif;
}
.h2Text span{
    color: #211006;
    font-size: 100px;
    font-weight: 900;
    font-family: 'Montserrat', sans-serif;
    text-transform: capitalize;
    margin: 0;
}

@media screen and (max-width: 1366px){
    .h2Text span{
        display: flex;
        flex-direction: row;
        justify-content: center;
        font-size: 80px;
    }
}
